.blur {
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  position: fixed;
  background: rgba(0, 0, 0, 0.24);
  z-index: 1000;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  padding: 0 20px;
  opacity: 0;
  visibility: hidden;
  transition: 0.3s;
  overflow-y: scroll;
}

.blur > {
  cursor: pointer !important;
}

.blur.active {
  opacity: 1;
  visibility: visible;
}

.popup {
  opacity: 0;
  transition: 0.2s;
  visibility: hidden;
  transition: 0.2s;
  display: flex;
  flex-direction: column;
}

.popup.active {
  opacity: 1;
  visibility: visible;
}

.popup img:first-child {
  z-index: -1;
  position: absolute;
  top: 0;
  left: 0;
  transform: translate(-50%, -50%);
}

.popup img:nth-child(2) {
  z-index: -1;
  position: absolute;
  bottom: 0;
  right: 0;
  transform: translate(50%, 50%);
}

.modal__close {
  cursor: pointer;
}

.modal__close svg {
  transition: 0.2s;
}

.modal__close:hover svg {
  transform: rotate(90deg);
}

